@import url(https://fonts.googleapis.com/css2?family=Mansalva&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app-nav{
  display: flex;
  justify-content: space-around;
  flex-direction: row-reverse;
  align-items: center;
  background-color: #DCCEFF;
  font-family:'Franklin Gothic Medium', cursive;
  font-weight: bolder;
  padding: 0 20px;
  height: 40px;
  width: auto;
}

.website-title {
  display: flex;
 color: #674D80 ;
}

footer{
  display: flex;
  justify-content:center;;
  align-items:center;
  background-color: #DCCEFF;
  font-family: 'Franklin Gothic Medium', cursive;
  color:  #674D80;
  font-weight: bolder;
}

body{
  background-color:#F6EEFF;
}




.artist-list-heading{
   display: flex;
   justify-content: center;
   color: #898092;
   font-family: 'Franklin Gothic Medium', cursive;
  }

  .artist-list-display{
      display: flex;
      justify-content: space-around;
      flex-direction: column;
      align-items: center;
      font-family: 'Franklin Gothic Medium', cursive;
      /* border: 2px  solid #DCCEFF ; */
      /* outline: 2px dashed #674D80 ; */
  }

  .more-info{
      color:#898092;
      font-family: 'Franklin Gothic Medium', cursive;
      display: flex;
      justify-content:center;   
  }

  .artist-data-title,
   .artist-data-description{
    color:#898092;
    font-family: 'Franklin Gothic Medium', cursive;
    display: flex;
    justify-content:center;
   
}
  .artist-data-description{
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #898092;
    font-family: 'Franklin Gothic Medium';
    font-weight:lighter;
    padding: 10px;
    margin: 8px;

  }
  

  @media only screen and (min-width: 768px) {
    aside {
       width: 30%;
       height: 100%;
     }
 }
.media-title{
    display:flex;
    justify-content: center;
    color: #898092;
    font-family: 'Franklin Gothic Medium', cursive;
    ;
}

.article-list-display{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    font-family: 'Franklin Gothic Medium', cursive;
    /* border: 2px  solid #DCCEFF ; */
}

h1{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    font-family: 'Franklin Gothic Medium', cursive;
    color: #898092;
   
}

.heading-detail{
  display:flex;
  justify-content: center;
  color: #898092;
  font-family: 'Franklin Gothic Medium', cursive;
}

.article-title{
  display:flex;
  justify-content: center;
  color: #898092;
  font-family: 'Franklin Gothic Medium', cursive;
  font-size: x-large;
}

.article-date {
  display:flex;
  justify-content: center;
  color: #898092;
  font-family: 'Franklin Gothic Medium';
  font-size:large;
}

.article-piece{
  display:flex;
  justify-content:flex-start;
  color: #898092;
  font-family: 'Franklin Gothic Medium';
  font-size: large;
  padding: 10px;
  margin: 30px 30px;
}



